@import url(https://fonts.googleapis.com/css?family=Dosis|Great+Vibes|Sacramento);
@import url(https://fonts.googleapis.com/css?family=Dosis);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  width: 100vw;
 
  
}







.header{
    margin:5% 20% 2% 20%;
}

h1{
    font-family: 'Dosis', sans-serif;
    letter-spacing: 4px;
    font-size: 35px;
    border-bottom: 2px solid grey;
    margin-bottom: 5%;
}


h1>span{
    font-family: 'Great Vibes', cursive;
    font-size: 55px;
}

p{
    font-family: 'Dosis', sans-serif;
    font-size: 24px;
}
button{
    font-family: 'Dosis', sans-serif;
    font-size: 20px;
    font-weight: bold;
    letter-spacing: 3px;
    width:220px;
    height:50px;
    margin: 2%;
    border-color:grey;
    background-color: white;
    position: relative; 
    cursor: pointer;
    transition: 0.2s;
    text-transform: uppercase;
    

}
button::after{
    content: '';
    position: absolute;
    width:226px;
    height: 56px;
    top:-7px;
    left:-7px;
    border:2px solid #00ACC1;  
}

button:hover{
 -webkit-transform: scale(1.1);
         transform: scale(1.1);
}
.users{
  display: flex;
  flex-wrap: wrap;
  margin-left: 15vw;
  margin-right: 15vw;
 
}

.userList>p{
  font-size:22px;
}

img{
  margin:30px 20px 0 20px;
  transition: 0.3s;
  cursor: pointer;
}
img:hover{
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.userList>p{
  font-size: 18px;
}


